* {
  font-family: Helvetica, sans-serif;
}

.card-grid {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}